<template>
    <div id="login-screen" :style="{'background-color': $store.state.c1}">
        <div class="container">
            <Title :color="$store.state.c4" />

            <p class="text-center font-weight-bold" :style="{'color': $store.state.c4}">Vous devez vous connecter pour jouer</p>
        
            <div class="row">
                <div class="col">
                    <LoginComponent v-if="mode == 'login'" @switch="change_mode" />
                    <RegisterComponent v-else @switch="change_mode" />
                </div>
            </div>

            <div :style="{'height': '50px'}"></div>
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import LoginComponent from '@/components/user/LoginComponent.vue'
import RegisterComponent from '@/components/user/RegisterComponent.vue'
import Title from '@/components/elements/Title.vue'

export default {
    components: {
        LoginComponent,
        RegisterComponent,
        Title
    },
    props: {

    },
    data () {
        return {
            mode: "login"
        }
    },
    methods: {
        change_mode(data) {
            this.mode = data
        }
    },
}
</script>

<style scoped>
#login-screen {
    width: 100dvw;
    height: 100dvh;
    overflow: auto;
}
</style>