<template>
  <LoadingApp v-if="app_status == 'loading'" />
  <ErrorLoadingApp v-else-if="app_status == 'error'" />
  <Login v-else-if="app_status == 'login'" />
  <div v-else id="theapp" class="h-100" :style="{'background-color': $store.state.c1}">
    <router-view :key="$route.fullPath" />
  </div>
</template>

<script>
import axios from 'axios'

import LoadingApp from '@/components/home/LoadingApp.vue'
import ErrorLoadingApp from '@/components/home/ErrorLoadingApp.vue'
import Login from '@/components/user/Login.vue'

export default {
  name: 'App',
  components: {
    LoadingApp,
    ErrorLoadingApp,
    Login
  },
  data() {
    return {
      app_status: "loading"
    }
  },
  methods: {
    get_user_data() {
      let token = localStorage.getItem("pendu_token")
      axios
        .get(this.$store.state.url_b+"/api/user/token?token="+token)
        .then(res => {
            if (res.data.success) {
              localStorage.setItem("pendu_token", res.data.token)
              localStorage.setItem("pendu_username", res.data.user_data.username)
              localStorage.setItem("pendu_email", res.data.user_data.email)
              this.app_status = "game"
            }
            else {
              this.app_status = "login"
            }
        })
        .catch(error => {
            console.log(error)
            this.app_status = "error"
        })
        .finally(() => {

        })
    }
  },
  created() {
    document.querySelector('meta[name="theme-color"]').setAttribute('content',  this.$store.state.c1)
    this.$store.dispatch('set_is_in_game', false)
    // Confirm if really quiting the game
    this.$router.beforeEach((to, from, next) => {
      if (this.$store.state.is_in_game) {
        const answer = window.confirm('Voulez-vous vraiment quitter cette page ?')
        if (answer) {
          next()
        }
        else {
          next(false)
        }
      }
      else {
        next()
      }
    })

    // Get user data
    this.get_user_data()
  }
}
</script>

