<template>
    <div class="mt-3 d-flex flex-column align-items-center rounded p-2" :style="{'background-color': $store.state.c2}">
        <h5 class="text-center" :style="{'color': $store.state.c4}">Inscrivez-vous</h5>

        <div class="my-3 col-md-6" :style="{'color': $store.state.c4}">
            <div class="my-2">
                <label for="register-pseudo">Pseudo</label>
                <input class="form-control" id="register-pseudo" name="pseudo" type="text" autocomplete="off" v-model="username" :disabled="loading">
            </div>

            <div class="my-2">
                <label for="register-email">Email</label>
                <input class="form-control" id="register-email" name="email" type="email" autocomplete="off" v-model="email" :disabled="loading">
            </div>

            <div class="my-2">
                <label for="register-password">Mot de passe</label>
                <input class="form-control" id="register-password" name="text" type="password" autocomplete="off" v-model="password" :disabled="loading">
            </div>

            <div class="my-2">
                <label for="register-password-2">Confirmer le mot de passe</label>
                <input class="form-control" id="register-password-2" name="text" type="password" autocomplete="off" placeholder="Retapez le mot de passe" v-model="password_2" :disabled="loading">
            </div>
        </div>

        <div v-if="msg" class="w-100 mb-3">
            <p class="text-center m-0 font-weight-bold font-italic" :style="{'color': $store.state.cdanger}">{{msg}}</p>
        </div>

        <div class="d-flex justify-content-center">
            <Btn :c1="$store.state.c3" :c2="$store.state.c1" :text="'S\'inscrire'" :is_disabled="loading" :is_loading="loading" @click="click_register()" />
        </div>

        <div class="d-flex justify-content-center my-3">
            <Btn :c1="$store.state.c1" :c2="$store.state.c4" :text="'Vous avez déjà un compte ? Se connecter'" :is_sm="true" :is_disabled="loading" @click="switch_mode('login')" />
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        Btn
    },
    props: {

    },
    data () {
        return {
            username: '',
            email: '',
            password: '',
            password_2: '',
            msg: '',
            USERNAME_MAX_LENGTH: 20,
            EMAIL_MAX_LENGTH: 100,
            PASSWORD_MIN_LENGTH: 6,
            PASSWORD_MAX_LENGTH: 30,
            loading: false
        }
    },
    methods: {
        switch_mode(mode="login") {
            this.$emit("switch", mode)
        },
        form_is_ok() {
            this.username = this.username.trim()
            this.email = this.email.trim()

            if (!this.username || this.username.length > this.USERNAME_MAX_LENGTH) {
                this.msg = "Le pseudo doit être entre 1 et " + this.USERNAME_MAX_LENGTH + " caractères"
                return false
            }

            if (!/^[\w]+$/.test(this.username)) {
                this.msg = "Pseudo: alphanumérique (sans accent) ou \"_\" uniquement"
                return false
            }

            if (!this.email || this.email.length > this.EMAIL_MAX_LENGTH) {
                this.msg = "L'email doit être entre 1 et " + this.EMAIL_MAX_LENGTH + " caractères"
                return false
            }

            // let re = /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/
            let re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
            if (!re.test(this.email)) {
                this.msg = "Email valide requis"
                return false
            }

            if (this.password.length < this.PASSWORD_MIN_LENGTH || this.password.length > this.PASSWORD_MAX_LENGTH) {
                this.msg = "Le mot de passe doit être entre " + this.PASSWORD_MIN_LENGTH + " et " + this.PASSWORD_MAX_LENGTH + " caractères"
                return false
            }

            if (this.password != this.password_2) {
                this.msg = "Les deux mots de passe doivent être identiques"
                return false
            }
            
            return true
        },
        click_register() {
            this.msg = ''
            if (this.form_is_ok()) {
                this.loading = true

                let data = {
                    username: this.username,
                    email: this.email,
                    password: this.password
                }

                axios
                    .post(this.$store.state.url_b+"/api/user/register", data)
                    .then(res => {
                        localStorage.setItem("pendu_token", res.data.token)
                        location.reload()
                    })
                    .catch(error => {
                        console.log(error)
                        this.msg = "Une erreur s'est produite"
                        if (error.response) {
                            if (error.response.status == 406) {
                                if (error.response.data == "username_exists") {
                                    this.msg = "Le pseudo est déjà utilisé"
                                }
                                else if (error.response.data == "email_exists") {
                                    this.msg = "L'email est déjà utilisé"
                                }
                            }
                        }
                    })
                    .finally(() => {
                        this.loading = false
                    })
            }
        }
    },
}
</script>

