<template>
    <div v-if="about == 'right_letter'" class="p-2 mb-3" :style="{'color': $store.state.csuccess}">
        <p class="font-weight-bold text-center m-0">
            Oui ! Il y a {{ data.nb }} {{ data.nb > 1 ? "lettres" : "lettre"}} {{ data.letter }}
        </p>
        <p class="font-weight-bold text-center m-0">
            <i class="fa-regular fa-star"></i> + {{data.nb}} {{data.combo > 0 ? "+ combo ("+data.combo+")" : ''}}
        </p>
    </div>
    <div v-else-if="about == 'wrong_letter'" class="p-2 mb-3" :style="{'color': $store.state.cdanger}">
        <p class="font-weight-bold text-center m-0">
            Pas de lettre {{data.letter}}
        </p>
        <p class="font-weight-bold text-center m-0">
            <i class="fa-solid fa-heart"></i> - 1
        </p>
    </div>
    <div v-else-if="about == 'wrong_word'" class="p-2 mb-3" :style="{'color': $store.state.cdanger}">
        <p class="font-weight-bold text-center m-0">
            Pas de lettre {{data.letter}}
        </p>
        <p class="font-weight-bold text-center m-0">
            <i class="fa-solid fa-heart"></i> - 1
        </p>
        <p class="font-weight-bold text-center m-0">
            Le mot était : {{data.word_name.toUpperCase()}}
        </p>
        <p class="font-italic text-center m-0" :style="{'color': $store.state.c4}">
            {{data.word_meaning}}
        </p>
        <div class="d-flex justify-content-center mt-2">
            <Btn :c1="$store.state.c3" :c2="$store.state.c1" :text="'Recommencer'" :is_sm="true" :cl="'fa-solid fa-arrow-rotate-left'" @click="click_restart()" />
        </div>
    </div>
    <div v-else-if="about == 'right_word'" class="p-2 mb-3" :style="{'color': $store.state.csuccess}">
        <p class="font-weight-bold text-center m-0">
            Oui ! Il y a {{ data.nb }} {{ data.nb > 1 ? "lettres" : "lettre"}} {{ data.letter }}
        </p>
        <p class="font-weight-bold text-center m-0">
            <i class="fa-regular fa-star"></i> + {{data.nb}} {{data.combo > 0 ? "+ combo ("+data.combo+")" : ''}}
        </p>
        <p class="font-italic text-center m-0" :style="{'color': $store.state.c4}">
            {{data.word_meaning}}
        </p>
        <div class="d-flex justify-content-center mt-2">
            <Btn :c1="$store.state.c3" :c2="$store.state.c1" :text="'Mot suivant'" :is_sm="true" :cl="'fa-solid fa-rocket'" @click="click_next_word()" />
        </div>
    </div>
    <div v-else-if="about == 'loading'" class="p-2 mb-3 d-flex justify-content-center" :style="{'color': $store.state.c4}">
        <div class="spinner-border spinner-border-sm" role="status">
            <span class="sr-only">Loading...</span>
        </div>
    </div>
    <div v-else-if="about == 'error'" class="p-2 mb-3" :style="{'color': $store.state.cdanger}">
        <p class="font-weight-bold text-center m-0">
            Une erreur s'est produite
        </p>
        <p class="font-weight-bold text-center m-0">
            Vérifiez votre connexion internet
        </p>
    </div>
    <div v-else>

    </div>
</template>

<script>
// import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        Btn
    },
    props: {
        about: {
            type: String,
            default: "any"
        },
        data: {
            default: {
                nb: 0,
                letter: '',
                combo: 0,
                word_name: '',
                word_meaning: ''
            }
        }
    },
    data () {
        return {

        }
    },
    methods: {
        click_restart() {
            this.$emit("restart", true)
        },
        click_next_word() {
            this.$emit("next", true)
        }
    },
}
</script>

