<template>
    <div v-if="parameter_active" class="d-flex justify-content-center align-items-center" id="config-screen">
        <div class="col-md-6 p-2 m-2 rounded" :style="{'color': $store.state.c4, 'background-color': $store.state.c2}">
            <h5 class="text-center mb-5">
                <i class="fa-solid fa-gear"></i> Paramètre
            </h5>
            <div class="p-2 mb-3">
                <p class="m-0 font-weight-bold text-center">{{ user_data.username }}</p>
                <p class="m-0 text-center">{{ user_data.email }}</p>
            </div>
            <div class="">
                <div class="m-2">
                    <router-link to="/" class="btn btn-block" :style="{'background-color': $store.state.c3, 'color': $store.state.c1}">
                        <i class="fa-solid fa-house"></i> Aller à l'accueil
                    </router-link>
                </div>

                <div class="m-2">
                    <Btn :c1="$store.state.cdanger" :c2="$store.state.c4" :text="'Annuler'" :cl="'fa-solid fa-xmark'" class="btn-block" @click="state_parameter(false)" />
                </div>
            </div>
        </div>
    </div>
    <div v-if="status == 'loading'" class="">
        <div class="py-5 d-flex flex-column justify-content-center align-items-center" :style="{'color': $store.state.c4}">
            <div class="spinner-border" role="status">
                <span class="sr-only">Loading...</span>
            </div>
            <p class="text-center m-2 font-weight-bold">Commencement de la partie <i class="fa-solid fa-wand-magic-sparkles"></i></p>
        </div>
    </div>
    <div v-else-if="status == 'error'" class="">
        <div class="py-5 d-flex flex-column justify-content-center align-items-center" :style="{'color': $store.state.cdanger}">
            <p class="text-center m-0 my-3">Une erreur s'est produite <i class="fa-regular fa-face-sad-tear"></i></p>
            <router-link to="/" class="btn btn-sm" :style="{'background-color': $store.state.c3, 'color': $store.state.c1}">
                <i class="fa-solid fa-house"></i> Aller à l'accueil
            </router-link>
        </div>
    </div>
    <div v-else class="">
        <div class="d-flex justify-content-between align-items-center" :style="{'background-color': $store.state.c2}">
            <Btn :cl="'fa-solid fa-gear'" :style="{'visibility': 'hidden'}" />
            <p class="font-weight-bold text-center p-0 m-0" :style="{'color': $store.state.c4}">Mot n°{{game.nb_word}}</p>
            <Btn :c1="$store.state.c2" :c2="$store.state.c4" :cl="'fa-solid fa-gear'" @click="state_parameter(true)" />
        </div>

        <div class="">
            <div class="" :style="{'color': $store.state.c4}">
                <p class="m-0 my-2 d-flex justify-content-end">
                    <span>
                        <span class="font-weight-bold">SCORE : </span>
                        <span>{{game.score}} {{message.about == "right_word" ? "+ " + game.points : ""}}</span>
                    </span>
                </p>
            </div>

            <div class="d-flex justify-content-end">
                <div class="">
                    <span v-for="heart in 6" :key="heart" class="heart" :style="{'color': $store.state.cheart}">
                        <i v-if="heart <= game.current_life" class="fa-solid fa-heart"></i>
                        <i v-else class="fa-regular fa-heart"></i>
                    </span>
                </div>
            </div>

            <div class="" :style="{'color': $store.state.c4}">
                <p class="m-0 d-flex flex-column align-items-start">
                    <span><i class="fa-regular fa-star"></i> <span class="font-weight-bold">Points à gagner :</span> {{game.points}}</span>
                    <span v-if="game.combo > 0" class="badge" :style="{'background-color': $store.state.c2, 'color': $store.state.c4}">Combo : {{game.combo}}</span>
                </p>
            </div>
        </div>

        <div class="">
            <div class="word-container d-flex justify-content-center py-3">
                <span v-for="letter, i in game.word" :key="i" class="letter d-flex justify-content-center align-items-center font-weight-bold" 
                    :class="{'alphanumeric': letter == '*' || letter.toLowerCase() != letter.toUpperCase()}"
                    :style="{'color': $store.state.c4, 'border-color': $store.state.c2}"
                >
                    {{letter == '*' ? '' : letter}}
                </span>
            </div>
        </div>

        <Message :about="message.about" :data="message.data" @restart="do_restart()" @next="go_to_next_word()" />

        <div class="d-flex justify-content-center btns-container py-3 rounded" :style="{'background-color': $store.state.c2}">
            <Btn v-for="l in letters" :key="l" :c1="get_btn_c1(l)" :c2="$store.state.c1" :text="l" class="button-letter font-weight-bold" :is_disabled="is_btn_disabled(l)" @click="click_letter(l)" />
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'
import Message from '@/components/game/elements/Message.vue'

export default {
    components: {
        Btn,
        Message
    },
    props: {
        mode: {
            type: String,
            default: "solo"
        }
    },
    data () {
        return {
            game: {
                MAX_LIFE: 6,
                current_life: 6,
                combo: 0,
                points: 0,
                score: 0,
                nb_word: 1,
                word: "",
                rights: [],
                wrongs: []
            },
            letters: 'ABCDEFGHIJKLMNOPQRSTUVWXYZ',
            parameter_active: false,
            status: "loading",
            game_id: 0,
            btn_disabled: false,
            message: {
                about: "any",
                data: {
                    nb: 0,
                    letter: '',
                    combo: 0,
                    word_name: "",
                    word_meaning: ""
                }
            },
            new_game: {},
            user_data: {
                "username": "",
                "email": ""
            }
        }
    },
    methods: {
        state_parameter(state = true) {
            this.parameter_active = state
        },
        get_btn_c1(letter) {
            if (this.game.rights.includes(letter)) {
                return this.$store.state.cbtnright
            }
            if (this.game.wrongs.includes(letter)) {
                return this.$store.state.cbtnwrong
            }
            return this.$store.state.c3 
        },
        is_btn_disabled(letter) {
            if (this.btn_disabled) {
                return true
            }
            if (this.game.rights.includes(letter) || this.game.wrongs.includes(letter)) {
                return true
            }
            return false
        },
        click_letter(letter) {
            this.btn_disabled = true
            this.message.about = "loading"
            let data = {
                letter: letter,
                game_id: this.game_id,
                token: localStorage.getItem("pendu_token")
            }
            axios
                .post(this.$store.state.url_b+"/api/game/try", data)
                .then(res => {
                    // console.log(res.data)
                    this.game = res.data.step
                    this.message = res.data.message
                    if (["right_letter", "wrong_letter"].includes(res.data.message.about)) {
                        this.btn_disabled = false
                    }
                    if (res.data.message.about == "right_word") {
                        this.new_game = res.data.new_game
                    }
                })
                .catch(error => {
                    console.log(error)
                    this.message.about = "error"
                    this.btn_disabled = false
                })
                .finally(() => {

                })
        },
        start() {
            let data = {
                token: localStorage.getItem("pendu_token"),
                mode: this.mode
            }
            axios
                .post(this.$store.state.url_b+"/api/game/new", data)
                .then(res => {
                    this.game_id = res.data.game_id
                    this.game = res.data.step
                    this.status = "game"
                })
                .catch(error => {
                    console.log(error)
                    this.status = "error"
                })
                .finally(() => {

                })
        },
        do_restart() {
            this.status = "loading"
            this.message.about = "any"
            this.btn_disabled = false
            this.start()
        },
        go_to_next_word() {
            this.message.about = "any"
            this.btn_disabled = false
            this.game = this.new_game
        },
        before_window_unload(e) {
            e.preventDefault()
            let return_value = "Vous êtes sur le point de quitter cette partie"
            e.returnValue = return_value
            return return_value
        },
    },
    created() {
        this.start()
        this.$store.dispatch('set_is_in_game', true)
        window.addEventListener('beforeunload', this.before_window_unload)
        this.user_data.username = localStorage.getItem("pendu_username")
        this.user_data.email = localStorage.getItem("pendu_email")
    },
    beforeUnmount() {
        this.$store.dispatch('set_is_in_game', false)
        window.removeEventListener('beforeunload', this.before_window_unload)
    }
}
</script>

<style scoped>
.heart {
    font-size: 18px;
    margin: 1px;
}

.word-container {
    flex-wrap: wrap;
}

.letter {
    width: 9vw;
    max-width: 60px;
    aspect-ratio: 1/1;
    margin: 1px;
}

.letter.alphanumeric {
    border: solid 3px;
}

.btns-container {
    flex-wrap: wrap;
}

.button-letter {
    margin: 3px;
}

#config-screen {
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    width: 100dvw;
    height: 100dvh;
    overflow: auto;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(10px);
}
</style>