<template>
    <div class="mt-3 d-flex flex-column align-items-center rounded p-2" :style="{'background-color': $store.state.c2}">
        <h5 class="text-center" :style="{'color': $store.state.c4}">Connectez-vous</h5>

        <div class="my-3 col-md-6" :style="{'color': $store.state.c4}">
            <div class="my-2">
                <label for="login-pseudo">Pseudo ou email</label>
                <input class="form-control" id="login-pseudo" name="email" type="email" autocomplete="off" v-model="username" :disabled="loading">
            </div>

            <div class="my-2">
                <label for="login-password">Mot de passe</label>
                <input class="form-control" id="login-password" name="text" type="password" autocomplete="off" v-model="password" :disabled="loading">
            </div>
        </div>

        <div v-if="msg" class="w-100 mb-3">
            <p class="text-center m-0 font-weight-bold font-italic" :style="{'color': $store.state.cdanger}">{{msg}}</p>
        </div>

        <div class="d-flex justify-content-center">
            <Btn :c1="$store.state.c3" :c2="$store.state.c1" :text="'Se connecter'" :is_disabled="loading" :is_loading="loading" @click="click_login()" />
        </div>

        <div class="d-flex justify-content-center my-3">
            <Btn :c1="$store.state.c1" :c2="$store.state.c4" :text="'Pas de compte ? S\'inscrire'" :is_sm="true" :is_disabled="loading" @click="switch_mode('register')" />
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        Btn
    },
    props: {

    },
    data () {
        return {
            username: '',
            password: '',
            msg: '',
            EMAIL_MAX_LENGTH: 100,
            PASSWORD_MIN_LENGTH: 6,
            PASSWORD_MAX_LENGTH: 30,
            loading: false
        }
    },
    methods: {
        switch_mode(mode="login") {
            this.$emit("switch", mode)
        },
        form_is_ok() {
            this.username = this.username.trim()

            if (!this.username || this.username.length > this.EMAIL_MAX_LENGTH) {
                this.msg = "Le pseudo/email doit être entre 1 et " + this.EMAIL_MAX_LENGTH + " caractères"
                return false
            }

            if (this.password.length < this.PASSWORD_MIN_LENGTH || this.password.length > this.PASSWORD_MAX_LENGTH) {
                this.msg = "Le mot de passe doit être entre " + this.PASSWORD_MIN_LENGTH + " et " + this.PASSWORD_MAX_LENGTH + " caractères"
                return false
            }
            
            return true
        },
        click_login() {
            this.msg = ''
            if (this.form_is_ok()) {
                this.loading = true

                let data = {
                    username: this.username,
                    password: this.password
                }

                axios
                    .post(this.$store.state.url_b+"/api/user/login", data)
                    .then(res => {
                        localStorage.setItem("pendu_token", res.data.token)
                        location.reload()
                    })
                    .catch(error => {
                        console.log(error)
                        this.msg = "Une erreur s'est produite"
                        if (error.response) {
                            if (error.response.status == 406) {
                                if (error.response.data == "username_not_found") {
                                    this.msg = "Le pseudo ou l'email n'existe pas"
                                }
                                else if (error.response.data == "wrong_password") {
                                    this.msg = "Le mot de passe est incorrect"
                                }
                            }
                        }
                    })
                    .finally(() => {
                        this.loading = false
                    })
            }
        }
    },
}
</script>

