<template>
    <div v-if="parameter_active" class="d-flex justify-content-center align-items-center" id="config-screen">
        <div class="col-md-6 p-2 m-2 rounded" :style="{'color': $store.state.c4, 'background-color': $store.state.c2}">
            <h5 class="text-center mb-3">
                <i class="fa-solid fa-circle-user"></i> Votre compte
            </h5>
            <div class="p-2 my-3">
                <p class="m-0 font-weight-bold text-center">{{ user_data.username }}</p>
                <p class="m-0 text-center">{{ user_data.email }}</p>
            </div>
            <div class="">
                <div class="m-2">
                    <Btn :c1="$store.state.c1" :c2="$store.state.c4" :text="'Se déconnecter'" :cl="'fa-solid fa-door-open'" class="btn-block" @click="logout()" />
                </div>

                <div class="m-2">
                    <Btn :c1="$store.state.cdanger" :c2="$store.state.c4" :text="'Annuler'" :cl="'fa-solid fa-xmark'" class="btn-block" @click="state_parameter(false)" />
                </div>
            </div>
        </div>
    </div>

    <div class="container">
        <div class="row">
            <div class="col d-flex justify-content-between align-items-center">
                <Btn :cl="'fa-solid fa-circle-user'" :style="{'visibility': 'hidden'}" />
                <Title :color="$store.state.c4" />
                <Btn :c1="$store.state.c1" :c2="$store.state.c4" :cl="'fa-solid fa-circle-user'" @click="state_parameter(true)" />
            </div>
        </div>
        <div class="row my-4">
            <div class="col">
                <p class="text-center" :style="{'color': $store.state.c4}">
                    Prêt à défier votre esprit?<br>
                    Plongez dans le monde du jeu du Pendu, où deviner le mot secret devient une aventure captivante!<br>
                    Devinez, jouez, gagnez!
                </p>
            </div>
        </div>
        <div class="row d-flex justify-content-center my-4">
            <div class="col-md-4 font-weight-bold d-flex flex-column align-items-center p-2 rounded m-2" :style="{'color': $store.state.c4, 'background-color': $store.state.c2}">
                <p class="text-center m-0">Votre meilleur score</p>
                <div v-if="best_score == 'loading'" class="spinner-border spinner-border-sm" role="status">
                    <span class="sr-only">Loading...</span>
                </div>
                <span v-else-if="best_score == 'no_score'" class="text-center font-italic">Pas encore de score</span>
                <span v-else-if="best_score == 'error'" class="text-center font-italic" :style="{'color': $store.state.cdanger}">Une erreur s'est produite</span>
                <span v-else class="">{{best_score}}</span>
            </div>
        </div>
        <div class="row d-flex justify-content-center my-2">
            <div class="col-md-2">
                <div class="">
                    <router-link to="/game" class="btn btn-block" :style="{'background-color': $store.state.c3, 'color': $store.state.c1}">
                        <i class="fa-solid fa-gamepad"></i> Jouer
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Title from '@/components/elements/Title.vue'
import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        Title,
        Btn
    },
    data () {
        return {
            parameter_active: false,
            user_data: {
                "username": "",
                "email": ""
            },
            best_score: "loading"
        }
    },
    methods: {
        state_parameter(state = true) {
            this.parameter_active = state
        },
        logout() {
            localStorage.setItem("pendu_token", null)
            localStorage.setItem("pendu_username", null)
            localStorage.setItem("pendu_email", null)
            location.reload()
        },
        load_best_score() {
            let data = {
                token: localStorage.getItem("pendu_token")
            }
            axios
                .post(this.$store.state.url_b+"/api/game/best-score", data)
                .then(res => {
                    if (res.data.best_score >= 0) {
                        this.best_score = res.data.best_score
                    }
                    else {
                        this.best_score = "no_score"
                    }
                })
                .catch(error => {
                    console.log(error)
                    this.best_score = "error"
                })
                .finally(() => {

                })
        }
    },
    created() {
        this.user_data.username = localStorage.getItem("pendu_username")
        this.user_data.email = localStorage.getItem("pendu_email")
        this.load_best_score()
    }
}
</script>

<style scoped>
#config-screen {
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    width: 100dvw;
    height: 100dvh;
    overflow: auto;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(10px);
}
</style>