import { createStore } from 'vuex'

export default createStore({
  state: {
    token: "loading",
    // url_b: 'http://' + window.location.hostname + ':5000',
    url_b: 'https://pendu.inonary.com',
    c1: '#191825',
    c2: '#262A56',
    c3: '#B8621B',
    c4: '#E3CCAE',
    cheart: '#DC3545',
    cdanger: '#DC3545',
    csuccess: '#28a745',
    cwarning: '#A73121',
    cbtnright: '#28a745',
    cbtnwrong: '#DC3545',
    is_in_game: false
  },
  mutations: {
    SET_TOKEN(state, payload) {
      state.token = payload
    },
    SET_IS_IN_GAME(state, payload) {
      state.is_in_game = payload
    }
  },
  actions: {
    set_token(context, token) {
      context.commit('SET_TOKEN', token)
    },
    set_is_in_game(context, is_in) {
      context.commit('SET_IS_IN_GAME', is_in)
    }
  },
  modules: {
  }
})
